/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-imports */
import React, { useState, useEffect } from "react";
import { Link } from 'react-router-dom';
import { Form, Card, Col } from "react-bootstrap";
import { Paper } from "@material-ui/core";
import { Modal } from "antd";
import ButtonLoading from '../../partials/common/ButtonLoading';
import makeRequest from '../../libs/request';
import { showErrorMessage, showSuccessMessageIcon } from '../../actions/notification';
import { validateEmail } from '../../libs/utils';

const AddEditUser = (props) => {
    const [dataAdd, setData] = useState({});
    const [isSubmiting, setLoadSubmit] = useState(false);

    const [dataChangePass, setDataChangePass] = useState({ visible: false });
    const [isLoadChange, setLoadChange] = useState(false);
    const updateId = props.match.params.id;

    useEffect(() => {

        if (updateId) {
            getUserByID(updateId);
        } else {
            setData();
        }

    }, [updateId]);
    const getUserByID = (id) => {
        makeRequest('get', `user/getbyIdNoPass?id=${id}`).then(({ data }) => {
            if (data.signal) {
                setData(data.data);

            }
        })
    }

    const onChangeValue = (key, value) => {
        setData({
            ...dataAdd,
            [key]: value
        })
    }


    // Change Pass Button
    const showModalChangePass = (id) => {
        dataAdd.oldPassword = '';
        dataAdd.password = '';
        dataAdd.password2 = '';
        setDataChangePass({
            id,
            visible: true,
        });
    };
    const hideModalChangePass = () => {
        setDataChangePass({
            ...dataChangePass,
            visible: false,
            idDel: 0,
        });
    };
    const changePassAction = () => {
        if (!dataAdd.oldPassword) {
            return showErrorMessage('Vui lòng nhập mật khẩu cũ!');
        }
        if (!dataAdd.password) {
            return showErrorMessage('Vui lòng nhập mật khẩu mới!');
        }
        if ((dataAdd.password === dataAdd.oldPassword)) {
            return showErrorMessage('Mật khẩu mới giống mật khẩu cũ, nhập mật khẩu mới!');
        }
        if (!dataAdd.password2) {
            return showErrorMessage('Vui lòng nhập lại mật khẩu mới!');
        }
        if (!(dataAdd.password === dataAdd.password2)) {
            return showErrorMessage('Nhập lại mật khẩu mới sai!');
        }
        setLoadChange(true);
        setLoadChange(false);
        makeRequest("post", `user/changePass`, dataAdd)
            .then(({ data }) => {
                if (data.signal) {
                    hideModalChangePass();
                    setLoadChange(false);
                    showSuccessMessageIcon("Thay đổi thành công");
                }
                else {
                    showErrorMessage(data.message);
                }
            })
            .catch((err) => {
                setLoadChange(false);
            });
    };

    ////
    const handleSubmit = (e) => {
        e.preventDefault();
        if (!dataAdd) {
            return showErrorMessage('Vui lòng nhập tên người dùng!')
        }
        if (!dataAdd.name) {
            return showErrorMessage('Vui lòng nhập tên người dùng!');
        }
        if (!dataAdd.email) {
            return showErrorMessage('Vui lòng nhập email người dùng!');
        }
        if (!validateEmail(dataAdd.email)) {
            return showErrorMessage('Không đúng định dạng email!')
        }

        if (!updateId) {
            if (!dataAdd.password) {
                return showErrorMessage('Vui lòng nhập mật khẩu người dùng!');
            }
        }
        if (!dataAdd.phone) {
            return showErrorMessage('Vui lòng nhập số điện thoại người dùng!');
        }
        setLoadSubmit(true);
        if (!updateId) {
            makeRequest('post', `user/create`, dataAdd).then(({ data }) => {
                if (data.signal) {
                    showSuccessMessageIcon('Thêm người dùng thành công');
                    return props.history.push('/admin/user/list');
                }

            }).catch(err => {
                setLoadSubmit(false);
                return showErrorMessage('Thêm thất bại, ' + err);
            })
        } else {
            makeRequest('post', `user/update`, dataAdd).then(({ data }) => {
                if (data.signal) {
                    showSuccessMessageIcon('Cập nhật người dùng thành công');
                    return props.history.push('/admin/user/list');
                }
            }).catch(err => {
                setLoadSubmit(false);
                return showErrorMessage('Cập nhật thất bại, ' + err);
            })
        }
    }

    return (
        <>
            <div className="row">
                <div className="col-md-12">
                    <div className="kt-section">
                        <Paper elevation={4}>
                            <Card >
                                <Card.Body>
                                    <Form onSubmit={handleSubmit}>
                                        <Form.Row>
                                            <Form.Group as={Col} >
                                                <Form.Label className="starDanger">Tên người dùng</Form.Label>
                                                <Paper elevation={2}>
                                                    <Form.Control
                                                        type="text" maxLength={255}
                                                        placeholder="Nhập tên người dùng"
                                                        value={(dataAdd && dataAdd.name) || ''}
                                                        onChange={(e) => onChangeValue('name', e.target.value)} />
                                                </Paper>
                                            </Form.Group>
                                        </Form.Row>
                                        <Form.Row>
                                            <Form.Group as={Col} >
                                                <Form.Label className="starDanger">Email Người Dùng</Form.Label>
                                                <Paper elevation={2}>
                                                    <Form.Control
                                                        type="text" maxLength={255}
                                                        placeholder="Nhập email người dùng"
                                                        value={(dataAdd && dataAdd.email) || ''}
                                                        onChange={(e) => onChangeValue('email', e.target.value)} />
                                                </Paper>
                                            </Form.Group>
                                        </Form.Row>
                                        {updateId ? '' : (<Form.Row>
                                            <Form.Group as={Col} >
                                                <Form.Label className="starDanger">Mật Khẩu</Form.Label>
                                                <Paper elevation={2}>
                                                    <Form.Control
                                                        type="password" maxLength={255}
                                                        placeholder="Nhập mật khẩu người dùng"
                                                        value={(dataAdd && dataAdd.password) || ''}
                                                        onChange={(e) => onChangeValue('password', e.target.value)} />
                                                </Paper>
                                            </Form.Group>
                                        </Form.Row>
                                        )}
                                        <Form.Row>
                                            <Form.Group as={Col} >
                                                <Form.Label className="starDanger">Số điện thoại</Form.Label>
                                                <Paper elevation={2}>
                                                    <Form.Control
                                                        type="text" maxLength={255}
                                                        placeholder="Nhập số điện thoại người dùng"
                                                        value={(dataAdd && dataAdd.phone) || ''}
                                                        onChange={(e) => onChangeValue('phone', e.target.value)} />
                                                </Paper>
                                            </Form.Group>
                                        </Form.Row>

                                        <div className="kt-login__actions">
                                            <Link to="/admin/user/list" style={{ marginRight: '5px' }}>
                                                <button type="button" className="btn btn-secondary btn-elevate kt-login__btn-secondary">Huỷ</button>
                                            </Link>
                                            <ButtonLoading loading={isSubmiting} className='btn btn-primary'>
                                                {!updateId ? 'Thêm' : 'Cập nhật'}
                                            </ButtonLoading>
                                            {!updateId ? '' :
                                                (
                                                    <button
                                                        type="button"
                                                        className=" btn btn-primary"
                                                        style={{ color: "white", marginLeft: "5px" }}
                                                        onClick={() => showModalChangePass(updateId)}
                                                    >
                                                        Đổi Mật Khẩu
                                                    </button>)}
                                        </div>
                                    </Form>
                                </Card.Body>
                            </Card>
                            <Modal
                                title="Đổi Mật Khẩu"
                                visible={dataChangePass.visible}
                                onOk={changePassAction}
                                onCancel={hideModalChangePass}
                                footer={[
                                    <ButtonLoading
                                        type="default"
                                        onClick={hideModalChangePass}
                                        className="btn btn-label-secondary btn-secondary"
                                    >
                                        Đóng
                </ButtonLoading>,
                                    <ButtonLoading
                                        className="btn btn-label-danger btn-danger"
                                        onClick={changePassAction}
                                        loading={isLoadChange}
                                    >
                                        <span>Thay đổi</span>
                                    </ButtonLoading>,
                                ]}
                            >
                                <Form.Group as={Col} >
                                    <Form.Label className="starDanger">Mật Khẩu Cũ</Form.Label>
                                    <Paper elevation={2}>
                                        <Form.Control
                                            type="text" maxLength={255}
                                            placeholder="Nhập mật khẩu cũ"
                                            value={(dataAdd && dataAdd.oldPassword) || ''}
                                            onChange={(e) => onChangeValue('oldPassword', e.target.value)} />
                                    </Paper>
                                </Form.Group>
                                <Form.Group as={Col} >
                                    <Form.Label className="starDanger">Mật Khẩu Mới</Form.Label>
                                    <Paper elevation={2}>
                                        <Form.Control
                                            type="text" maxLength={255}
                                            placeholder="Nhập mật khẩu mới"
                                            value={(dataAdd && dataAdd.password) || ''}
                                            onChange={(e) => onChangeValue('password', e.target.value)} />
                                    </Paper>
                                </Form.Group>
                                <Form.Group as={Col} >
                                    <Form.Label className="starDanger">Nhập lại mật khẩu mới</Form.Label>
                                    <Paper elevation={2}>
                                        <Form.Control
                                            type="text" maxLength={255}
                                            placeholder="Nhập lại mật khẩu mới"
                                            value={(dataAdd && dataAdd.password2) || ''}
                                            onChange={(e) => onChangeValue('password2', e.target.value)} />
                                    </Paper>
                                </Form.Group>
                            </Modal>
                        </Paper>
                    </div>
                </div>

            </div>
        </>
    );
}

export default AddEditUser;